module.exports = {"de":{"translation":{"Look through the window":"Guck durch das Fenster","Language":"Sprache","Joey's Blog":"Das Blog von Joey","I am passionate about development in Python and Data Science.":"Ich bin begeistert von Python Softwareentwicklung und Data Science.","My hobbies are reading, coding and studying about investments.":"Ich lese, programmiere und studiere über Investitionen gerne.","...and everyone knows I love Oscar Wilde &#38; Fernando Pessoa":"...und alle weiß, dass ich Oscar Wilde &#38; Fernando Pessoa liebe","I am in love with Germany since my exchange in Dresden, Saxony.":"Ich bin seit meinem Austausch in Dresden (Sachsen) verliebt in Deutschland.","PS: I &hearts; Spekulatius":"PS: Ich &hearts; Spekulatius","Technologies I used to develop this website":"Ich habe diese Technologien verwendet, um diese Website zu entwickeln","Other Technologies I have experience with":"Andere Technologien, mit denen ich Erfahrung habe","since":"seit","Previous":"Früher","Next":"Nächster"}},"en":{"translation-bio":{"#bio-literature-card":"I am fascinated by the written word. I have a lovely time reading poetry, but I feel like I can relate more to Portuguese and Brazilian works mostly for motives of culture and rhythm of our language. For plays, short stories and novels, most of my favorites titles are actually from the UK, France, Italy and some from the US and Brazil, naturally.","#bio-meetup-card":"I enjoy learning new things and getting to know people who are interested in similar Tech topics. Joining \"Meetups\" was just an obvious thing for me. The main groups I join are: <br>- PyData Berlin <br>- Python Users Berlin","#bio-investments-card":"Early in life, I was taught by dad (and life) to save for my future and I also saw men and women in my family having their own small businesses but I never had someone to teach me how to invest, so I now explore that by myself. However, my #1 dream is: be responsible for the creation of many jobs in a company people will be happy to work for."},"translation-data-visualization":{"#data-visualization-intro":"Working with data, exploring a dataframe, ploting interesting findings... I just love it! When I am not learning about Machine Learning techniques and technologies, I am playing with Data Visualization tools. I just find it very exciting how much information you can extract from a good dataset (specially when I am involved since the data collection process) and how much data visualization strategies help people better understand our data."},"translation-portfolio":{"#portfolio-this-website":"This website was created with VueJs and Bootstrap 4. For a full list of the technologies I used here, visit the TechStack page.","#portfolio-coding-challenges":"I have my most recent coding challenges solutions (mostly in C or Python) in my <a href='https://github.com/Joeffison/coding_challenges#coding-challenges' target='__blank'>Github repository<a/>.","#portfolio-ml-builder":"In 2017 I created a Proof of Concept of an idea I had to help students get started exploring Machine Learning. I used Python/Django and Angular 2. The full code source is on my Github and a mocked version is available on <a href='https://joeffison.github.io/MachineLearningBuilder/' target='__blank'>https://joeffison.github.io/MachineLearningBuilder<a/>","#portfolio-instagram-utility":"Website built with Angular 2 to provide some features not officially offered by Instagram. <a href='https://joeffison.github.io/InstagramUtility/' target='__blank'>https://joeffison.github.io/InstagramUtility<a/>","#portfolio-academic-research":"During my Research, our team at SPLab created the \"RealTime PCO\" tool (to run tests in Embedded Real-Time Sysytems) which led to our paper \"Automating Test Case Creation and Execution for Embedded Real-time Systems\" describing our proposed process to test critical systems, whose time constraints are highly important (e.g., airplaines systems)."},"translation":{}},"pt":{"translation-bio":{"#bio-literature-card":"Eu sou fascinado pela palavra escrita. Ler poesia me dá um prazer imenso. Embora para a poesia eu prefira as escritas em português, eu também aprecio a literatura originária de outros lugares (e.g., Reino Unido, França, Itália e Estados Unidos).","#bio-meetup-card":"Eu gosto de aprender coisas novas e conhecer pessoas interessadas em tópicos técnicos similares, então participar de \"Meetups\" foi apenas natural. Os principais grupos dos quais eu participo são: <br>- PyData Berlin <br>- Python Users Berlin","#bio-investments-card":"Logo cedo, eu fui ensinado pelo meu pai (e pela vida) a poupar para o meu futuro e também vi homens e mulheres na minha família abrirem os seus próprios empreendimentos, mas eu nunca tive alguém que pudesse me ensinar a investir, por isso agora exploro isso eu mesmo. No entanto, meu sonho #1 é: ser responsável pela criação de empregos em uma empresa na qual as pessoas terão prazer em trabalhar."},"translation-data-visualization":{"#data-visualization-intro":"Trabalhar com dados, explorar dataframes, criar gráficos com descobertas interessantes... eu simplesmente adoro tudo isso! Quando eu não estou estudando sobre novas tecnologias ou técnicas de Aprendizagem de Máquina, eu gosto de brincar com ferramentas de Visualização de Dados. Eu acho muito estimulante quanta informação pode ser extraída de um bom conjunto de dados (especialmente se eu estiver envolvido desde a coleta desses dados) e o quanto as estratégias de visualização de dados nos ajudam a melhor compreender o que é evidenciado pelos dados."},"translation-portfolio":{"#portfolio-this-website":"Este site foi criado do zero, com VueJs e Bootstrap 4. Para a lista completa de tecnologias que eu usei, acesse a página TechStack.","#portfolio-coding-challenges":"As minhas soluções mais recentes para desafios de programação (em sua maioria em C ou Python) estão no meu <a href='https://github.com/Joeffison/coding_challenges#coding-challenges' target='__blank'>repositório do Github<a/>.","#portfolio-ml-builder":"Em 2017, eu construi uma \"Prova de Conceito\" de uma ideia que tive para ajudar estudantes no início de suas explorações em Machine Learning. Para tal, eu usei Python/Django e Angular 2. Todo o código fonte está no meu Github e uma versão (mock) está disponível em <a href='https://joeffison.github.io/MachineLearningBuilder/' target='__blank'>https://joeffison.github.io/MachineLearningBuilder<a/>","#portfolio-instagram-utility":"Website criado com Angular 2 para fornecer algumas ferramentas ainda não disponíveis oficialmente pelo Instagram. <a href='https://joeffison.github.io/InstagramUtility/' target='__blank'>https://joeffison.github.io/InstagramUtility<a/>","#portfolio-academic-research":"Durante meu período de Pesquisa, nosso time do SPLab criou o \"RealTime PCO\" (ferramenta para testar sistemas críticos embarcados de tempo-real) que esultou no artigo científico \"Automating Test Case Creation and Execution for Embedded Real-time Systems\" descrevendo o processo proposto para testar sistemas críticos, para os quais os requisitos de tempo são extremamente importantes (e.g., sistemas de aviões)."},"translation":{"Look through the window":"Olhe pela janela","Language":"Idioma","Joey's Blog":"O Blog do Joey","I am passionate about development in Python and Data Science.":"Eu sou fascinado por desenvolvimento em Python e DataScience.","My hobbies are reading, coding and studying about investments.":"Meus hobbies são ler, programar e estudar sobre investimentos.","...and everyone knows I love Oscar Wilde &#38; Fernando Pessoa":"...e todos sabem quanto eu amo Oscar Wilde e Fernando Pessoa","I am in love with Germany since my exchange in Dresden, Saxony.":"Eu sou apaixonado pela Alemanha desde o meu intercâmbio em Dresden, Saxônia.","PS: I &hearts; Spekulatius":"PS: Eu &hearts; Spekulatius","Technologies I used to develop this website":"Tecnologias que eu usei para criar este site","Other Technologies I have experience with":"Outras Tecnologias com as quais tenho experiência","since":"desde","Previous":"Anterior","Next":"Próximo","This website":"Este site","Coding Challenges":"Desafios de Programação","Academic Research":"Pesquisa Acadêmica","Work Experience":"Experiência Profissional","Academic Experience":"Experiência Acadêmica","Library":"Biblioteca","Check out my Portfolio":"Veja o meu Portfolio","Data Science":"Ciência de Dados"}}}


